@import url(https://fonts.googleapis.com/css2?family=Days+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Days+One&family=Secular+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.landing-page {
  align-items: center;
  background-image: url(/trybe-trybewallet-project/static/media/Business_Startup01.dc74ebcc.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  height: 100vh;
  justify-content: flex-start;
}

.login-form {
  align-items: center;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
  display: flex;
  flex-direction: column;
  height: 35vh;
  justify-content: space-evenly;
  margin: 0 100px 0 150px;
  -webkit-backdrop-filter: blur(6px);
  padding: 30px 45px 10px;
}

.greetings {
  color: rgb(255, 255, 255);
  margin: 0;
}

 /* https://getcssscan.com/css-buttons-examples */
.login-btn {
  background-color: transparent;
  background-color: initial;
  background-image: linear-gradient(-180deg, rgb(0, 215, 117), rgb(0, 189, 104));
  border: 0;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px;
  color: rgb(255, 255, 255);
  cursor: pointer;
  display: inline-block;
  height: 38px;
  outline: 0;
  overflow: hidden;
  -webkit-user-select: none;
  padding: 0 20px;
  pointer-events: auto;
  position: relative;
  text-align: center;
  touch-action: manipulation;
  -ms-user-select: none;
      user-select: none;
  vertical-align: top;
  white-space: nowrap;
  width: 100%;
  z-index: 9;
}

.login-btn:disabled {
  cursor: default;
  opacity: .8;
}

.login-btn:hover:enabled {
  background: #00bd68;
}

header {
  align-items: center;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  background-color: rgba(255, 255, 255, 0.05);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  display: flex;
  justify-content: space-between;
  -webkit-backdrop-filter: blur(6px);
  padding: 10px 20px;
}

.logo {
  align-items: center;
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
}

header h1 {
  font-family: 'Days One', sans-serif;
  font-size: 20px;
  margin: 0 0 0 10px;
}

header ul {
  align-items: flex-end;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 25%;
}

header li {
  color: rgb(255, 255, 255);
  font-size: 14px;
  /* letter-spacing: 1px; */
  margin: 0 10px;
}

.expenses-form {
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  margin: 20px 0;
  width: 100%;
}

.expenses-form select {
  margin-top: 8px;
}

.select-input::after {
  color: rgb(0, 0, 0);
  margin-top: 8px;
}

.expenses-form button {
  background-color: #13aa52;
  border: 1px solid #13aa52;
  font-size: 14px;
}


.table-section {
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 0 5%;
}

/* https://dev.to/dcodeyt/creating-beautiful-html-tables-with-css-428l */
table {
  border-collapse: collapse;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  font-size: 0.9em;
  margin: 25px 0;
  width: 100%;
}

table thead tr {
  background-color: rgb(0, 152, 121);
  color: rgb(255, 255, 255);
  text-align: left;
}

table :is( th, td ) {
  padding: 12px 15px;
}

table td {
  border-right: 1px solid rgb(255, 255, 255, .1);
}

table td:last-of-type {
  align-items: center;
  border: none;
  display: flex;
  justify-content: center;
}

table tbody tr {
  border-bottom: 1px solid rgb(255, 255, 255, .8);
}

table tbody tr:nth-of-type(even) {
  background-color: rgb(255, 255, 255, .5);
}

table tbody tr:last-of-type {
  border-bottom: 1px solid rgb(0, 152, 121, .7);
}

table tbody tr:hover {
  background-color: rgb(255, 255, 255, .7);
}

table tbody tr:nth-of-type(even):hover {
  background-color: rgb(255, 255, 255, .7);
}

table .expense-btn {
  background-color: rgb(255, 46, 39);
  border: 1px solid rgb(255, 46, 39);
}

.expenses-page {
  background-image: url(/trybe-trybewallet-project/static/media/Businessmen_are_trying_to_push_forward_the_icon_Dollar_money_up.9f1b1013.jpg);
  background-size:contain;
  background-position: center;
  height: 100vh;
}
* {
  font-family: 'Secular One', sans-serif;
}

.input-container {
  border: none;
  box-sizing: border-box;
  outline: none;
  padding: 0;
  position: relative;
}

.input-container label {
  color: rgb(255, 255, 255);
}

.input-container :is( input, select, option ) {
  background: transparent;
  border: none;
  border-bottom: solid 1px rgb(239, 239, 239);
  color: rgb(255, 255, 255);
  font-size: 14px;
  outline: none;
  width: 100%;
}

.input-container option {
  color: rgb(0, 0, 0)
}

.input-container input:focus {
  border: none;
  border-bottom: 1px solid rgb(0, 215, 117);
  outline: none;
 }

.input-container input::-webkit-input-placeholder {
  color:rgb(255, 255, 255, .3);
  font-size: 10pt;
}

.input-container input:-ms-input-placeholder {
  color:rgb(255, 255, 255, .3);
  font-size: 10pt;
}

.input-container input::-ms-input-placeholder {
  color:rgb(255, 255, 255, .3);
  font-size: 10pt;
}

.input-container input::placeholder {
  color:rgb(255, 255, 255, .3);
  font-size: 10pt;
}

.expense-btn {
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, .1) 0 2px 4px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-weight: 400;
  outline: none;
  outline: 0;
  padding: 5px 12.5px;
  text-align: center;
  transform: translateY(0);
  transition: transform 150ms, box-shadow 150ms;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.expense-btn:hover:enabled {
  box-shadow: rgba(0, 0, 0, .15) 0 3px 9px 0;
  transform: translateY(-2px);
}

.expense-btn:disabled {
  cursor: default;
  opacity: .8;
}

@media (min-width: 768px) {
  .expense-btn {
    padding: 5px 15px;
  }
}

.image-credit {
  bottom: 5px;
  font-size: 12px;
  position: fixed;
  right: 10px;
}

.image-credit a {
  color: rgb(0, 0, 0, .8);
  text-decoration: none;
}

.image-credit a:hover {
  color:rgb(0, 0, 0);
}

